@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

@mixin absCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


// MEDIA QUERY MANAGER
/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
@mixin respond($breakpoint) {
    @if $breakpoint == mobile-s {
        @media only screen and (max-width: 20em) { @content };    // <= 320px
    }
    @if $breakpoint == mobile-m {
        @media only screen and (max-width: 23.4375em) { @content };    // <= 375px
    }
    @if $breakpoint == mobile-l {
        @media only screen and (max-width: 26.5625em) { @content };    // <= 420px
    }
    @if $breakpoint == phone {
        @media only screen and (max-width: 37.5em) { @content };    // <= 600px
    }

    @if $breakpoint == t-800 {
        @media only screen and (max-width: 50.0em) { @content };   // <= 800px
    }

    @if $breakpoint == tab-port {
        @media only screen and (max-width: 56.25em) { @content };   // <= 900px
    }
  
    @if $breakpoint == tab-land {
        @media only screen and (max-width: 75em) { @content };    // <= 1200px
    }

    @if $breakpoint == desktop-small {
        @media only screen and (min-width: 75.1em) { @content };    // > 1200px
    }

    @if $breakpoint == desktop {
        @media only screen and (min-width: 87.5em) { @content };    // >= 1400px
    }
    @if $breakpoint == big-desktop {
        @media only screen and (min-width: 112.5em) { @content };    // >= 1800
    }
}

